@font-face {
    font-family: 'Museo';
    src: url('../fonts/Museo.woff') format('woff');
    font-style: normal;
}

svg{
    position: absolute;
    overflow: hidden;
    width: 600px;
    height: 600px;
}

#controls {
  position: absolute;
  top: 500px;
  left: 50%;
  width: 310px;
  transform: translate(-50%, 120px);
}

.grain{
    pointer-events: none;
}

#replay{
    cursor: pointer;
}


